import React from 'react'
import {FaRegWindowClose,
    FaRegCheckSquare,
    } from "react-icons/fa";
    const LotCardComponent = ({ sl,el, users, handleSoldstatus, formatTime, time ,index}) => (
      <tr>
              <td className="text-center">
                {el.lot}
                <br />
                {el.invoice}
              </td>
              <td className="text-center">
                {el.factory_name}
                <br />
                ({el.grade}) Rating: <span style={{ color: 'purple' }}>({el.lirating_name})</span>
<span style={{ color: 'navy' }}>({el.lrating_name})</span>

              </td>
              <td className="text-center">
                {el.price}
                <br />
                {`(${el.pkgs})`}
              </td>
              <td className="text-center">
              <div className="bidd_name">
                      <p className="highest">{el.maximum_bid}</p>
                      <span
                        className={
                          el.buyer == users.link_id ? "hname" : "rname"
                        }
                      >
                        {el.buyer_name}({el.maximum_bid - el.price})
                      </span>
                    </div>

                    <br />
              </td>

              <td className="text-center">
               
              <div className="col-md-12 d-flex justify-content-center align-items-center col-sm-12 col-xs-12">

                  <div className="">
                    <button
                      onClick={() => handleSoldstatus(el, 1)}
                      disabled={el.brokers === users.link_id ? false : true}
                      className={
                        el.sold_status === 1
                          ? "btn-success square-circle mr-4"
                          : el.sold_status === 2
                          ? " btn-warning square-circle mr-4"
                          : " btn-warning square-circle mr-4"
                      }
                    >
                      <FaRegCheckSquare className="bid" />
                    </button>
                    <button
                      onClick={() => handleSoldstatus(el, 0)}
                      disabled={el.brokers === users.link_id ? false : true}
                      className=" btn-danger square-circle"
                    >
                      <FaRegWindowClose className="bid" />
                    </button>
                  </div>
                </div>
              </td>
            </tr>
      );
      

export default LotCardComponent
