import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import music from "../../assets/message.mp3";
import notify from "../../assets/starttune.mp3";
import io from "socket.io-client";
import ModalBroker from "../../components/ModalBroker/Modal";
import ReactLoading from "react-loading";
import { throttle } from "lodash";
import { getAuctiondetails } from "../../store/auctionSlice";
import LotCardComponent from "../../components/reuseable/LotCardComponent";
import { useNavigate } from "react-router-dom";
import MessageForm from "../../components/Message/MessageForm";
import MessageDisplay from "../../components/Message/MessageDisplay";
const socket = io.connect(process.env.REACT_APP_SERVER_URL);
const ViewList = () => {
  const navigate = useNavigate();
  const [show2, setShow2] = useState(false);
  const API_URL = process.env.REACT_APP_SERVER_URL;
  const { auction_data } = useSelector((state) => state.auction);
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);
  const [checkedItemName, setCheckedItemName] = useState("");
  const [checkedItemid, setCheckedItemid] = useState("");
  const [activebroker, setActivebroker] = useState();
  const [list, setList] = useState([]);
  const [rawlist, setrawList] = useState([]);
  const [startIndex, setStartIndex] = useState(0);
  const [endIndex, setEndIndex] = useState(1);
  const [activeUsers, setActiveUsers] = useState(0);
  const [visible, setVisible] = useState(false);
  const [showPopup, setShowPopup] = useState(true);
  const [time, setTime] = useState(0);
  const [isRunning, setIsRunning] = useState(false);

  const [bidloading, setbidLoading] = useState(false);
  const [startBloading, setstartBloading] = useState(false);
  const dispatch = useDispatch();
//reAuction
const reAuction = async (el) => {
  try {
    setbidLoading(true);
    const id = el.id;
    // const value = status;

    await axios.put(`${API_URL}/updatereauction`, {
      id: id,
      // status: value,
    });

    const brokerResponse = await axios.get(`${API_URL}/getselectbroker`);
    socket.emit("user", brokerResponse.data[0].current_brokers);
    setbidLoading(false);

    socket.on("users", (data) => {
      // const filteredData = data.filter((item) => item.sold_status === 0);
      setList(data);
      setrawList(data);
    });
  } catch (error) {
    console.log(error);
  }
};


  const handleSoldstatus = async (el, status) => {
    try {
      setbidLoading(true);
      const id = el.id;
      const value = status;

      await axios.put(`${API_URL}/updatesold`, {
        id: id,
        status: value,
      });

      const brokerResponse = await axios.get(`${API_URL}/getselectbroker`);
      socket.emit("user", brokerResponse.data[0].current_brokers);
      setbidLoading(false);

      socket.on("users", (data) => {
        const filteredData = data.filter((item) => item.sold_status === 0);
        setList(data);
        setrawList(data);
      });
    } catch (error) {
      console.log(error);
    }
  };

  //bidders modal
  const openLot2 = async () => {
    try {
      const response = await axios.get(`${API_URL}/getselectbroker`);
      socket.emit("user", response.data[0].current_brokers);
      setShow2(true);
    } catch (error) {
      console.log(error);
    }
  };

  //visibility change for bidder
  const handleVisibilityChange = async () => {
    if (document.visibilityState === "visible") {
      try {
        const response = await axios.get(`${API_URL}/getselectbroker`);
        const { auction } = response.data[0];
        setShowPopup(auction === 0);
      } catch (error) {
        console.log(error);
      }
    }
  };
  // let timer = null;

const minimizeCheck = async () => {
  if (document.visibilityState === 'visible') {
    window.location.reload();
    // clearTimeout(timer);
  } else {
   
  }
};
const [countdownActive, setCountdownActive] = useState(false);

const onNext = () => {
 if (endIndex + 4 <= list.length) {
   const newStartIndex = startIndex + 4;
   const newEndIndex = endIndex + 4;

   setStartIndex(newStartIndex);
   setEndIndex(newEndIndex);

   socket.emit("updateIndexes", {
     startIndex: newStartIndex,
     endIndex: newEndIndex,
   });
   socket.off("updateIndexes");

   setCountdownActive(true); // Start the countdown again
 } else {
   stopCountdown(); // Call the stopCountdown function if the list reaches its end
 }
};


  // //on next
  // const onNext = () => {
  //   if (endIndex + 4 <= list.length) {
  //     const newStartIndex = startIndex + 4;
  //     const newEndIndex = endIndex + 4;

  //     setStartIndex(newStartIndex);
  //     setEndIndex(newEndIndex);

  //     socket.emit("updateIndexes", {
  //       startIndex: newStartIndex,
  //       endIndex: newEndIndex,
  //     });
  //     socket.off("updateIndexes");
  //   }
  // };

  //on previous
  const onPrevious = () => {
    if (startIndex - 4 >= 0) {
      console.log(startIndex, endIndex);
      const newStartIndex = startIndex - 4;
      const newEndIndex = endIndex - 4;
  
      setStartIndex(newStartIndex);
      setEndIndex(newEndIndex);
  
      socket.emit("updateIndexes", {
        startIndex: newStartIndex,
        endIndex: newEndIndex,
      });
      // socket.off("updateIndexes");
  
      // setCountdownActive(true); // Start the countdown again
    } else {
      // stopCountdown(); // Call the stopCountdown function if the list reaches its beginning
    }
  };
  

  const statrtAuc = async () => {
    try {
      await axios.post(`${API_URL}/startauc`);
    } catch (error) {
      console.log(error);
    }
  };

  const stopAuc = async () => {
    try {
      await axios.post(`${API_URL}/stopauc`);
    } catch (error) {
      console.log(error);
    }
  };

  const fetchData = async () => {
    try {
      const response = await axios.get(`${API_URL}/getselectbroker`);
      const { auction, current_brokers, name, id } = response.data[0];
  
      if (auction === 0) {
        setShowPopup(true);
      } else {
        setShowPopup(false);
      }
  
    
  
      await Promise.all([
        setCheckedItemName(name),
        setCheckedItemid(id),
        setActivebroker(current_brokers),
      
      ]);
  
      socket.emit("user", current_brokers);
      // socket.emit("user_toggle", current_brokers); need
  
      const userToggleListener = (userId) => {
        setVisible(userId === users.link_id);
      };
  
      socket.on("user_toggle", userToggleListener);
  
      // Cleanup function
      return () => {
        socket.off("users",current_brokers);
        // Remove the userToggleListener when the component unmounts
        socket.off("user_toggle", userToggleListener);
      };
    } catch (error) {
      console.log(error);
    }
  };
  

  useEffect(() => {

    let isMounted = true;

    const handleStart = () => {
      setIsRunning(true);
    };

    const handleStop = () => {
      setIsRunning(false);
    };

    const handleTimeUpdate = (newTime) => {
      if(newTime===0){
       const notifym = new Audio(notify);
       notifym.play()
      }
      setTime(newTime);
    };

    const throttledVisibilityChange = throttle(handleVisibilityChange, 1000);
    // socket.on("updateIndexes", ({ startIndex, endIndex }) => {
    //   // Handle the updated indexes received from the backend
    //   console.log("Received updated indexes:", startIndex, endIndex);
    //   setStartIndex(startIndex);
    //   setEndIndex(endIndex);
    //   // Perform any necessary operations with the updated indexes
    //   // Update your frontend UI or execute relevant logic based on the updated indexes
    // });
    socket.on("visibility", throttledVisibilityChange);
    socket.on("start", handleStart);
    socket.on("stop", handleStop);
    socket.on("time", handleTimeUpdate);
    // console.log('thn');
    fetchData();

    // dispatch(getuser());

    socket.on("users", (data) => {
      // console.log('thn');
      if (isMounted) {
        const filteredData = data.filter((item) => item.sold_status === 0);
        setList(data);
        setrawList(data);
      }
    });

    socket.on("checkname", (data) => {
      if (isMounted) {
        setCheckedItemName(data);
      }
    });

    return () => {
      isMounted = false;
      socket.off("users");
      socket.off("checkname");
      socket.off("visibility", throttledVisibilityChange);
      socket.off("start", handleStart);
      socket.off("stop", handleStop);
      socket.off("time", handleTimeUpdate);
    };
  }, [socket]);


  useEffect(() => {
    let timeoutId;

    const tick = () => {
      socket.emit("tick");
      timeoutId = setTimeout(tick, 1000);
    };

    if (isRunning) {
      tick();
    } else {
      clearTimeout(timeoutId);
    }

    return () => clearTimeout(timeoutId);
  }, [isRunning]);



  useEffect(() => {
    let isMounted = true;
    dispatch(getAuctiondetails());

    fetchLatestMessage();

  socket.on('newMessage', (message) => {
  setMessages(message)
  });
    const handleVisibilityChange = (value) => {
      setShowPopup(value);
      localStorage.setItem("isVisible", value);
    };
  
    const handleStart = () => {
      setIsRunning(true);
    };
  
    const handleStop = () => {
      setIsRunning(false);
    };
  
    const handleTimeUpdate = (newTime) => {
      setTime(newTime);
    };
  
    const handleActiveUsers = (count) => {
      setActiveUsers(count);
    };
  
    const handleStartIndex = (data) => {
      setStartIndex(data);
    };
  
    const handleEndIndex = (data) => {
      setEndIndex(data);
    };
      socket.on("updateIndexes", ({ startIndex, endIndex }) => {
      // Handle the updated indexes received from the backend
      console.log("Received updated indexes:", startIndex, endIndex);
      setStartIndex(startIndex);
      setEndIndex(endIndex);
      // Perform any necessary operations with the updated indexes
      // Update your frontend UI or execute relevant logic based on the updated indexes
    });
    socket.on("visibility", handleVisibilityChange);
    socket.on("start", handleStart);
    socket.on("stop", handleStop);
    socket.on("time", handleTimeUpdate);
    socket.on("activeUsers", handleActiveUsers);
    socket.on("startIndex", handleStartIndex);
    socket.on("endIndex", handleEndIndex);
    socket.emit("getObjects");
    // document.addEventListener("visibilitychange", minimizeCheck);

    return () => {
      isMounted = false;
      // clearTimeout(timeoutId);
      // document.removeEventListener("visibilitychange", minimizeCheck);
      socket.off("visibility", handleVisibilityChange);
      socket.off("start", handleStart);
      socket.off("stop", handleStop);
      socket.off("time", handleTimeUpdate);
      socket.off("activeUsers", handleActiveUsers);
      socket.off("startIndex", handleStartIndex);
      socket.off("endIndex", handleEndIndex);
      socket.off("getObjects");
      socket.off("updateIndexes");
      socket.off("newMessage");
       };
  }, []);
  
  
  
  const currentPage = Math.floor(startIndex / 4) + 1;
  const startCountdown = () => {
    socket.emit("start",list);
  //   setStartIndex(0);
  // setEndIndex(4);
  };
  

  const throttledStartCountdown = throttle(async () => {
    setstartBloading(true);
    await statrtAuc();
    setShowPopup(false);
    socket.emit("visibility", false);
    socket.once("play-sound", () => {
      const audio = new Audio(music);
      audio.play();
    });
    socket.emit("start",list);
    setstartBloading(false);
    // const countdownEndListener = () => {
    //   onNext(); // Call the onNext function when the countdown ends
    //   socket.off("countdownEnd", countdownEndListener); // Unsubscribe from the "countdownEnd" event
    // };
  
    // socket.on("countdownEnd", countdownEndListener);
    // setCountdownActive(true); // Start the countdown
  }, 1000);
  
  
  
  const stopCountdown = () => {
    throttledStopCountdown();
  };
  
  const throttledStopCountdown = throttle(async() => {
    await stopAuc();
    setShowPopup(true);
    socket.emit("visibility", true);
    socket.emit("stop");
  }, 1000); // Throttle the function with a delay of 1000 milliseconds
  

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60)
      .toString()
      .padStart(2, "0");
    const seconds = (time % 60).toString().padStart(2, "0");
    return `${minutes}:${seconds}`;
  };

  // const filteredData = rawlist.filter((item) => item.buyer === users.link_id);
  const filteredDatabrokers = list.slice(startIndex-4, endIndex-4 + 3);


// console.log(rawlist);
// messagesection
const [messages, setMessages] = useState([]);

// useEffect(() => {
//   fetchLatestMessage();

//   socket.on('newMessage', (message) => {
//   setMessages(message)
//   });
// }, []);
const fetchLatestMessage = () => {
  axios
    .get(`${API_URL}/messages/latest`) // Replace with your server endpoint
    .then((response) => {
      setMessages(response.data);
    })
    .catch((error) => console.log(error));
};

const handleMessageSubmit = (e,message) => {
  e.preventDefault();
  const short_name = users.short_name;
  axios
    .post(`${API_URL}/messages`, { message ,short_name}) // Replace with your server endpoint
    .then((response) => {
      setMessage('');
      // setMessages((prevMessages) => [...prevMessages, response.data]);
      socket.on('newMessage', (message) => {
        setMessages(message)
        });
    
    })
    .catch((error) => console.log(error));
};

useEffect(() => {
  const socket = io.connect(process.env.REACT_APP_SERVER_URL);
  // Listen for 'startIn' event
  socket.on('startIn', (startIndex) => {
    setStartIndex(startIndex);
  });

  // Listen for 'endIn' event
  socket.on('endIn', (endIndex) => {
    setEndIndex(endIndex);
  });

  // Listen for 'visibilitychange' event to handle page visibility changes
  const handleVisibilityChange = () => {
    if (!document.hidden) {
      // Page is visible, request the updated 'endIndex' value
      socket.emit('getEndIndex');
    }
  };

  document.addEventListener('visibilitychange', handleVisibilityChange);

  // Set up a timer to periodically request the updated 'endIndex' value
  const timer = setInterval(() => {
    socket.emit('getEndIndex');
  }, 5000); // Adjust the interval as needed

  // Clean up the socket connection and event listener when the component unmounts
  return () => {
    socket.disconnect();
    document.removeEventListener('visibilitychange', handleVisibilityChange);
    clearInterval(timer);
  };
}, []);
const [message, setMessage] = useState('');


  return (
    <>
      {bidloading ? (
        <>
          <div className="overlay">
            <div className="spinner-container">
              <div className="spinner"></div>
              <span>Verifying data...</span>
            </div>
          </div>
        </>
      ) : (
        ""
      )}

      {!rawlist[0] ? (
        <>
          <div
            style={{
              height: "100vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <ReactLoading
              type={"spin"}
              color={"#007bff"}
              height={50}
              width={50}
            />
            <div
              style={{
                marginTop: "20px",
                fontSize: "16px",
                fontWeight: "bold",
              }}
            >
              Verifying Data...
            </div>
          </div>
        </>
      ) : (
        <>

          <ModalBroker
            title="Brokers Lot"
            onClose={() => setShow2(false)}
            show={show2}
            filteredData={filteredDatabrokers}
            onV={handleSoldstatus}
            reAuc={reAuction}
          ></ModalBroker>


          <div className="section-head sec-h">
            <p className="br-n">={checkedItemName}=</p>
            <div className="row newc">
              <div className="col-7 col-md-9 card-btn leftCard">
                <h2>
                  Auction Date:{" "}
                  {auction_data.length === 0 ? "" : auction_data[0].date}{" "}
                </h2>
                <p>
                  SALE NO - #
                  {auction_data.length === 0 ? "" : auction_data[0].sale_no}{" "}
                  {activeUsers}{" "}
                </p>
                <p>
                  Season - #
                  {auction_data.length === 0 ? "" : auction_data[0].season}{" "}
                </p>
                <p>
                  CURRENT BROKER :{" "}
                  <span className="c_brokers">{checkedItemName}</span>{" "}
                </p>

                <div className=" row margin-left">
                  {users.type === "BROKERS" ? (
                    <button
                      type="button"
                      onClick={openLot2}
                      className="col-5  buttonGroup btn btn-primary  my-btn"
                    >
                      All
                    </button>
                  ) : (
                    ""
                  )}
                </div>
              </div>
              
              <div className="col-5 col-md-3 align-items-center justify-content-center mt-5 rCard">
                <div className="bflex card-btn rightButton">
                  <div className="button-row">
                    <button
                      disabled={startBloading}
                      type="button"
                      onClick={startCountdown}
                      className="buttonGroup btn btn-primary my-btn"
                    >
                      START
                    </button>
                    <button
                      type="button"
                      onClick={stopCountdown}
                      className="buttonGroup btn btn-danger my-btn"
                    >
                      STOP
                    </button>
                  </div>
                  <div className="button-row">
                    <button
                      type="button"
                      onClick={onPrevious}
                      className="buttonGroup btn btn-success my-btn"
                    >
                      PREV
                    </button>
                    <button
                      type="button"
                      onClick={onNext}
                      className="buttonGroup btn btn-success my-btn"
                    >
                      NEXT
                    </button>
                  </div>
                </div>

                <div className="col-12">
                  <div className="card">
                    <div className="row rightCard">
                      <div className=" ">
                        <div className="col-md-6 count-t">
                          <h2 className="text-center mh2">
                            {formatTime(time)}
                          </h2>
                        </div>
                        <div className="col-md-6 name-t">
                          <h2 className="mh2 text-center">
                            {users.short_name}
                          </h2>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <div className="card p-3">
      <div className="row">
        <div className="col-6 col-md-3 d-flex justify-content-start align-items-center">
        <div className="col">
    <label className="visually-hidden" htmlFor="inline-form-website">Message</label>
    <input
          className="form-control"
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Message"
        />  </div>
  <div className="col">
    <button type="submit" onClick={(e)=>handleMessageSubmit(e,message)} className="btn btn-primary">Send</button>
  </div>
        </div>
        <div className="col-6 col-md-9 d-flex justify-content-end">
          <span style={{ backgroundColor: 'red',color:'white', padding: '5px', fontSize: '12px' }}>Last Message</span>
          <span style={{ backgroundColor: 'white',color:'black', padding: '5px', fontSize: '12px' }}>{messages.message}</span>
          <span style={{ backgroundColor: 'blue',color:'white', padding: '5px', fontSize: '12px' }}>{messages.u_id}</span>
        </div>
      </div>
    </div>
          <h2 className="text-center p-h">-={users.type} PANEL=- </h2>
          <hr className="hr-h" />
          <div className="con">
            <div className="row g-1">
            <table id="auction" className="table table-bordered">
              <thead>
                <tr>
                  <th scope="col">
                    Lot <br />
                    Invoice
                  </th>
                  <th scope="col">
                    Factory
                    <br />
                    Grade
                  </th>
                  <th scope="col">
                    Price
                    <br />
                    Qty
                  </th>
                  <th scope="col">
                    Highest
                    <br /> Buyer
                  </th>

                  <th scope="col">Bid</th>
                </tr>
              </thead>
            <tbody>
              {list.slice(startIndex, endIndex + 3).map((el, index) => (
                <LotCardComponent
                sl={startIndex + index + 1}
                  key={el.id + 9}
                  el={el}
                  users={users}
                  handleSoldstatus={handleSoldstatus}
                  formatTime={formatTime}
                  time={time} // Add the time prop here
                />
              ))}
           </tbody>
           </table>
            </div>
            <div className="">
                <div className="row">
                  {list.slice(startIndex-4, endIndex-4 + 3).map((el, index) => (
                   <>
                   <div className="col-6 col-md-3" key={index}>
                    <div className="card">
                      <div className={el.sold_status===1?"rem-card-y":"rem-card-x"}>
                        <div className="row">
                          <div className="col-8">
                            <div className="col-12">
                              <p className="rem-text">{el.factory_name} ({el.lot})</p>
                            </div>
                            <div className="col-12">
                              <p className="rem-text">{el.buyer_name}({el.maximum_bid - el.price})</p>
                            </div>
                          </div>
                          <div className="col-4 d-flex align-items-center justify-content-center">
                            <div className="row">
                              <div className="col-6">
                                <i className="bi bi-sun"></i>
                              </div>
                              <div className="col-6">
                                <i className="bi bi-moon"></i>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                   </>
                  ))}
                </div>
              </div>
          </div>
        </>
      )}
    </>
  );
};

export default ViewList;
